	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.mcdonalds-nordheide.de/jobs\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/10/nordheidewobla-271020071.jpg\" alt=\"NordheideWobla_27102007.jpg\" border=\"0\" width=\"398\" height=\"412\" /></a></p>\n<p>To prepare for our McCaf&eacute; opening the first week of December, we placed an ad in the <a href=\"http://www.kreiszeitung-wochenblatt.de/\">Nordheide Wochenblatt</a>, our local weekly paper. We're looking for help for both the McCaf&eacute; and the restaurant, so the ad is not specific for either. In the first 24 hours we've already gotten a number of online applications. (This graphic includes a slight error... we're looking for several \"motivated employees\", not just one).</p>\n<p>We've also ordered a <a href=\"http://www.mcdonalds-nordheide.de/2007/10/25/unser-team-sucht-motivierte-mitarbeiter\">color recruitment poster</a> which we'll be posting in our lobby this week.</p>\n<p><a href=\"http://www.mcdonalds-nordheide.de/2007/10/25/unser-team-sucht-motivierte-mitarbeiter\"><img src=\"http://www.mcdonalds-nordheide.de/wordpress/wp-content/uploads/2007/10/mitarbeiter_plakat1.thumbnail.jpg\" title=\"mitarbeiter_plakat1.jpg\" alt=\"mitarbeiter_plakat1.jpg\" /></a></p>","fields":{"title":"Help Wanted","date":"28 Oct 2007","url":"https://papascott.de/2007-10-28-help-wanted/"}}},"pathContext":{"slug":"/2007-10-28-help-wanted/","prev":{"fields":{"title":"Street Soccer Heroes","date":"26 Oct 2007","slug":"/2007-10-26-street-soccer-heroes/"}},"next":{"fields":{"title":"Halloween Carving Kit","date":"30 Oct 2007","slug":"/2007-10-30-halloween-carving-kit/"}}}}

